.checkboxAuthWrapper {
  display: block;
  position: relative;
  width: max-content;
}
.checkboxAuthItemNone {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  height: 100%;
  opacity: 0;
  cursor: pointer;
  z-index: 2;
}
.checkboxAuthSpan {
  position: relative;
  margin-left: 0;
  display: flex;
  width: max-content;
  font-size: 100%;
  align-items: center;
  cursor: pointer;
  transition: 0.2s;
}
.checkboxAuthItemBlock {
  position: relative;
  transition: 0.2s;
  width: 10px;
  height: 10px;
  border-radius: 0;
  margin-right: 5px;
  border: 1px solid black;
}
.checkboxAuthName {
  white-space: nowrap;
  font-size: 80%;
  transition: 0.2s;
  color: black;
}
.checkboxAuthItem_1_1 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  overflow: hidden;
  opacity: 0;
  transition: 0.2s;
  background: black;
  border-radius: 0;
  width: 80%;
  height: 80%;
}
.checkboxAuthItemNone:checked + .checkboxAuthSpan .checkboxAuthItemBlock {
  border: 1px solid black;
}
.checkboxAuthItemNone:checked + .checkboxAuthSpan .checkboxAuthName {
}
.checkboxAuthWrapper:hover .checkboxAuthSpan .checkboxAuthItemBlock {
  border: 1px solid black;
}
.checkboxAuthWrapper:hover .checkboxAuthSpan .checkboxAuthName {
  color: black;
}
.checkboxAuthItemNone:checked + .checkboxAuthSpan .checkboxAuthItemBlock .checkboxAuthItem_1_1 {
  overflow: visible;
  opacity: 1;
}
@media (max-width: 800px) {}